@import '../../globals/constants.less';
@import '../../globals/responsive.less';

.button {
  cursor: pointer !important;
  border-radius: 4px;
  background: @base-blue;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5em;
  h4.button_text {
    color: white;
    margin: 0 auto;
    text-align: center;
    
    .button-children-wrapper {
      display: flex;
      justify-content: center;
      margin: 0 auto;
    }

    span {
      max-width: 90%;
    }
  }
  .desktop({
    &:hover {
      opacity: 0.85;
    }
  });
}



